.widget_list_entities {

  .list_spinner {

    padding: $list-spinner-image-padding;
    width: $list-spinner-image-width;
    height: $list-spinner-image-width;
    background: transparent url($spinner-path) 0 0 no-repeat;
    background-size: $list-spinner-image-width $list-spinner-image-width;
    margin-left: calc(50% - #{$list-spinner-image-width});

    &.on {

      display: block;
    }

    &.off {

      display: none;
    }
  }

  .page {

    .pm-list-item {

      border-bottom: 1px solid $list-item-color-ruler;
      min-width: $list-item-min-width;
      max-width: $list-item-max-width;
      clear: both;
      overflow: auto;
      padding: 20px 0 20px;

      .image-container {

        float: left;
        height: $list-item-image-container-dimensions;
        width: $list-item-image-container-dimensions;
        overflow: hidden;

        &.noimage {

          background-color: $list-item-image-background-color;
        }

        .initials {

          color: #fff;
          line-height: $list-item-image-container-dimensions;
          text-align: center;
        }

        .image {

          max-width: 100%;
          width: 100%;
        }
      }

      .text-ellipsis {

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      .info {

        position: relative;

        .info-position {

          position: absolute;
        }

        .visibility {

          @extend .info-position;
          top: 0;
          right: 0;
          cursor: pointer;

          .image {

            width: $list-item-info-arrow-width;
            height: $list-item-info-arrow-width;
            vertical-align: middle;
            margin-left: $list-item-info-arrow-both-sides-margin;
            margin-right: $list-item-info-arrow-width + $list-item-info-arrow-both-sides-margin;
            background: transparent url($arrow-path) 0 0 no-repeat;
            background-size: $list-item-info-arrow-width $list-item-info-arrow-width;
            display: inline-block;
          }

          &.opened {

            .link {

              display: none;
            }

            .arrow {

              @extend .image;
              transform: rotate(270deg);
            }

          }

          &.closed {

            .link {

              display: none;
            }

            .arrow {

              @extend .image;
              transform: rotate(90deg);
            }

          }

        }

        .main-data {

          @extend .info-position;

          left: $list-item-image-container-dimensions + $list-item-image-container-left-margin;
          bottom: -1 * $list-item-image-container-dimensions;

          .info-text {

            @extend .text-ellipsis;
            width: $list-item-info-text-max-width;
          }

          .fullname,
          .name {

            @extend .info-text;
            font-weight: bold;
          }

          .jobtitle {

            @extend .info-text;
          }

          .company {

            @extend .info-text;
            font-style: italic;
          }

        }

      }

      .details {

        &.opened {

          display: block;

          clear: both;
          padding-top: 25px;

          .website,
          .company-website,
          .country {

            @extend .text-ellipsis;
            width: $list-item-details-text-max-width;
          }

          .profile {

            &.filled {

              padding-top: 25px;
            }
          }

        }

        &.closed {

          display: none;
        }
      }
    }
  }
}
