$arrow-path: "../../img/arrow.png";
$spinner-path: "../../img/spinner.gif";

$small-screen: 600px;

$small-screen-initials-font-size: 30px;
$small-screen-details-text-font-size: 16px;

$normal-screen-initials-font-size: 30px;
$normal-screen-info-link-font-size: 10px;
$normal-screen-details-text-font-size: 14px;

$list-spinner-image-padding: 20px;
$list-spinner-image-width: 50px;

$list-item-min-width: 335px;
$list-item-max-width: 500px;

$list-item-image-container-dimensions: 100px;
$list-item-image-container-left-margin: 35px;
$list-item-image-background-color: rgb(89, 89, 89);

$list-item-info-text-max-width: $list-item-min-width - $list-item-image-container-dimensions - ($list-item-image-container-left-margin * 2);
$list-item-details-text-max-width: $list-item-min-width - $list-item-image-container-left-margin;

$list-item-info-arrow-width: 15px;
$list-item-info-arrow-both-sides-margin: 10px;

$list-item-color-ruler: rgb(175, 173, 174);

$pagination-button-dimensions: 24px;
$pagination-button-color: #999999;
$pagination-button-background-color: #efefef;
$pagination-button-color-active: white;
$pagination-button-background-color-active: #bbbbbb;
$pagination-font-size: 14px;