
.widget_list_entities .pmwpp-list-pagination {

  display: block;
  overflow: hidden;
  padding: 0 5px 5px 0;
  margin: 25px 0;

  span {

    float: left;
    color: $pagination-button-color;
    font-size: $pagination-font-size;
    line-height: $pagination-button-dimensions;
    font-weight: normal;
    text-align: center;
    min-width: $pagination-font-size;
    padding: 0 7px;
    margin: 0 5px 0 0;
    border-radius: 3px;
    background: $pagination-button-background-color;
  }

  a {

    @extend span;
  }

  ul {

    list-style: none;
    padding: 0;
    margin: 0;
    border: 0;

    li {

      list-style: none;
      padding: 0;
      margin: 0;
      border: 0;
      float: left;

      .current {

        background: $pagination-button-background-color-active;
        color: $pagination-button-color-active;
        cursor: default;
      }

      .next {

        width: $pagination-button-dimensions;
        height: $pagination-button-dimensions;
        background-image: url($arrow-path);
        background-position: center center;
        background-repeat: no-repeat;
        background-size: 50%;
      }

      .prev {

        @extend .next;
        transform: rotate(180deg);
      }

      .ellipse {

        background: none;
        border: none;
        border-radius: 0;
        font-weight: bold;
        cursor: default;

        &.clickable {

          cursor: pointer;
        }

        input {

          width: 3em;
        }
      }
    }
  }
}