.widget_list_entities {

  .initials {

    @media screen and (min-width: $small-screen) {

      font-size: $normal-screen-initials-font-size;
    }

    @media screen and (max-width: $small-screen - 1) {

      font-size: $small-screen-initials-font-size;
    }
  }

  .page {

    .pm-list-item {

      .text-ellipsis {

        @media screen and (min-width: $small-screen) {

          font-size: $normal-screen-details-text-font-size;
        }

        @media screen and (max-width: $small-screen - 1) {

          font-size: $small-screen-details-text-font-size;
        }
      }

      .info {

        .visibility {

          &.opened {

            .link {

              @media screen and (min-width: $small-screen) {

                font-size: $normal-screen-info-link-font-size;
              }

              &.close-details {

                @media screen and (min-width: $small-screen) {

                  display: inline-block;
                }
              }
            }
          }

          &.closed {

            .link {

              @media screen and (min-width: $small-screen) {

                font-size: $normal-screen-info-link-font-size;
              }

              &.open-details {

                @media screen and (min-width: $small-screen) {

                  display: inline-block;
                }
              }
            }
          }
        }

        .main-data {

          .fullname,
          .name,
          .jobtitle,
          .company {

            @media screen and (min-width: $small-screen) {

              font-size: $normal-screen-details-text-font-size;
            }

            @media screen and (max-width: $small-screen - 1) {

              font-size: $small-screen-details-text-font-size;
            }
          }
        }
      }

      .details {

        .website,
        .company-website,
        .country,
        .profile {

          @media screen and (min-width: $small-screen) {

            font-size: $normal-screen-details-text-font-size;
          }

          @media screen and (max-width: $small-screen - 1) {

            font-size: $small-screen-details-text-font-size;
          }
        }
      }
    }
  }
}